import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthService } from '@core/auth/auth.service';
import { environment } from 'environments/environment';
import { Observable } from 'rxjs';
import { Response } from '@shared/models/response-with-token';

@Injectable()
export class UnidadesFuncionalesService {
    constructor(private http: HttpClient, private _authService: AuthService) { }

    /**
     * 
     * @param idunidadfuncional
     * @returns 
     */
    reporteOrdenServicio(idunidadfuncional: number): Observable<Response> {
        return this.http.get<Response>(`${environment.reporte_orden_servicio}?idunidadfuncional=` + idunidadfuncional);
    }
}